import SkuForm from './SkuForm.vue'
import { createApp } from 'vue'
import { ElCard, ElCheckbox, ElInput, ElButton, ElTable, ElTableColumn, ElTooltip, ElForm, ElFormItem } from 'element-plus'

const registerElementPlus = app => {
    const hasNamed = ElButton && ElTable && ElForm && ElCard && ElCheckbox && ElInput && ElTableColumn && ElTooltip && ElFormItem
    if (hasNamed) {
        app.use(ElCard)
        app.use(ElCheckbox)
        app.use(ElInput)
        app.use(ElButton)
        app.use(ElTable)
        app.use(ElTableColumn)
        app.use(ElTooltip)
        app.use(ElForm)
        app.use(ElFormItem)
    } else if (typeof globalThis !== 'undefined' && globalThis.ElementPlus) {
        app.use(globalThis.ElementPlus)
    }
}

SkuForm.install = app => {
    registerElementPlus(app)
    app.component(SkuForm.name, SkuForm)
}

export const createAppWithSkuForm = (options = {}) => {
    const app = createApp(options)
    registerElementPlus(app)
    app.component(SkuForm.name, SkuForm)
    return app
}

export default SkuForm
